.categoriesTab{
  background-color: rgb(30, 30, 30);
  color:rgb(207, 207, 207);
  border: none;
  padding: 0px 20px 0px 0px;
  text-align: left;
  font-weight: bolder;
  margin-left:  0px;
  margin-right:  0px;
  margin-top:  15px;
  cursor: pointer;
  font-size: 32px;
  outline:none;
}

.categoriesTab:focus {
  outline: 0 !important;
}

.contentStatuses{
  background-color: rgb(0, 0, 0);
  border: 1px solid #FFF;
  color: white;
  padding: 4px 8px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin-left:  0px;
  margin-right:  0px;
  margin-top:  15px;
  cursor: pointer;
  font-size: 14px;
}

.cardUser{
  width: 250px;
  height: 75px;
  margin-right: 5px;
  margin-left: 5px;
  margin-bottom: 0px;
  display: inline-block;
}

.cardUserImage{
  height: 75px;
  width: 75px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #333333;
  display: inline-block;
  margin: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.cardUserText{
  padding: 10px;
  text-align: center;
  vertical-align: top;
  color:rgb(207, 207, 207);
  font-weight: 200;
  background-color: #333333;
  width: 175px;
  height: 75px;
  display: inline-block;
  margin: 0;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.friendsBlock{
  display: flex;
  flex-direction: row;
}

.fActivityBlock{
  display: inline-flex;
  margin-bottom: 0px;
  margin-top: 10px;
  margin-right: 10px;
  background-color: rgb(43, 43, 43);
  color: rgb(199, 199, 199);
  vertical-align: top;
  width: 550px;
  border-radius: 5px;
  padding-bottom: 10px;
}

.fActivityUser{
  display: inline-block;
  padding-top: 10px;
  padding-left: 10px;
  margin-left: 0px;
  margin-right: 13px;
  vertical-align: top;
  font-weight: 400;
}

.fActivityInfo{
  display: inline-block;
  margin-bottom: 0px;
  margin-top: 0px;
  padding-top: 12px;
}

.fActivityInfoText{
    margin-bottom: 5px;
}

#reviewInput{
  color: rgb(207, 207, 207);
  background-color: rgb(30, 30, 30);
}

#spentTimeInput{
  color: rgb(207, 207, 207);
  background-color: rgb(30, 30, 30);
}

.md-form label.active {
  color: rgb(207, 207, 207) !important;
  background-color: rgb(30, 30, 30) !important;
}