.movieContentPage {
    padding: 1rem;
    background-color: rgb(30, 30, 30);
    color: rgb(207, 207, 207);
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 0px
}

.movieContentHeader{
    background-color: rgb(0, 0, 0);
    color: #fff;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 0px;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.movieContentBody{
    padding-left: 10px;
    padding-right: 10px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    padding-bottom: 15px;
}

.metacritic{
    vertical-align: middle;
    text-align: center;
    color: #fff!important;
    font-family: Arial,Helvetica,sans-serif;
    font-style: normal!important;
    font-weight: bold!important;
    font-size: 35px;
    background-color: rgb(102, 204, 51);
    height: 2em;
    width: 2em;
    line-height: 2em;
    border-radius: 6px
}

.metacriticText{
    font-family: Arial,Helvetica,sans-serif;
    font-size: 12px;
    text-align: center;
    padding-left: 3px;
}

.movieFriendsBlock{
    margin-left: 25px;
    margin-bottom: 20px;
}

.mainInfo{
    margin-bottom: 20px;
}

.mainInfo p{
    margin-bottom: 2px;
}

.posterBlock{
    text-align: center;
}